.header__logo {
    padding: 0 0 41px;
    background-image: url(../../../images/logo.svg);
    background-repeat: no-repeat;
    background-size: contain;
    width: 142px;
    height: 33px;
}

@media only screen and (max-width: 420px) {
    .header__logo {
        width: 104px;
        height: 25px;
        padding: 0 0;
    }
}